.game-page {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-areas:
    "playersInfo gameInfo"
    "playersInfo game";
  grid-template-rows: auto 1fr;
  grid-template-columns: 300px 1fr;
}

@media screen and (max-width: 720px) {
  .game-page {
    grid-template-areas:
      "playersInfo"
      "gameInfo"
      "game";
    grid-template-rows: auto auto 1fr;
    grid-template-columns: 1fr;
  }
  .player-info {
    display: flex;
    column-gap: 5px;
  }
}

.players-info {
  grid-area: playersInfo;
}
.game-info {
  grid-area: gameInfo;
  display: flex;
  align-items: center;
  padding: 10px;
  column-gap: 40px;
}
.refresh {
  cursor: pointer;
}
.hint {
  color: rgb(22, 179, 231);
}
.hint:hover {
  text-decoration: underline;
  cursor: pointer;
}

.score-wrapper {
  display: flex;
  align-items: center;
  column-gap: 5px;
  margin-bottom: 5px;
}
