@import url("https://fonts.googleapis.com/css2?family=Golos+Text&display=swap");
* {
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: "Golos Text";
}

body {
  background-color: #f2f3f7;
}

.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.players-info {
  flex-shrink: 0;
  padding: 10px;
}

.map-wrapper {
  flex-grow: 1;
  overflow: hidden;
  grid-area: game;
}

.items {
  display: flex;
  column-gap: 5px;
  margin-bottom: 5px;
}

.circle {
  width: 20px;
  height: 20px;
  background: red;
  border-radius: 100%;
}
.cube {
  width: 20px;
  height: 20px;
  background: red;
}

.form-page {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  position: relative;
  row-gap: 5px;
}

.modal-form-page {
  box-shadow: none;
  border-radius: unset;
}

.form-page-buttons {
  display: flex;
  justify-content: space-between;
}

.ask-modal {
  padding: 5px;
}

.ask-modal .list-item:hover {
  background: lightblue;
}
